  padding: 12px 24px;
  border-radius: 50px;
  border: 1px solid #514ebc;
  transition: all 0.3s ease-in-out;
}

.blue-button:hover {
  background-color: transparent;
  color: #514ebc;
  box-shadow: 0 4px 20px -5px rgba(0, 0, 0, 0.3);
}

.white-blue-button {
