.go-to-top {
  position: fixed;
  z-index: 4;
  right: 30px;
  bottom: 30px;
  display: none;
}

.go-to-top a {
  display: block;
  font-size: 12px;
  color: #232323;
  background-color: #fff;
  width: 40px;
  height: 40px;
  box-shadow: -1px 3px 20px rgba(0, 0, 0, 0.2);
  border-radius: 69% 31% 37% 63% / 64% 66% 34% 36%;
  padding: 9.4px 15.38px;
  opacity: 0.8;
}

.go-to-top a:hover {
  color: #514ebc;
  opacity: 1;
}

body::-webkit-scrollbar {
  width: 10px;
  display: none;
}

body.showscrollbar::-webkit-scrollbar {
  display: block;
}

body::-webkit-scrollbar-thumb {
  background-color: #514ebc;
  border-radius: 5px;
}

.preloader {
  display: flex;
  position: fixed;
  z-index: 10;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: #fff;
}

.preloader .spinner {
  position: relative;
  width: 80px;
  height: 80px;
  margin: auto;
}

.preloader .spinner .double-bounce1,
.preloader .spinner .double-bounce2 {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: #514ebc;
  border-radius: 50%;
  opacity: 0.6;
  animation: bounce 2.0s infinite ease-in-out;
}

.preloader .spinner .double-bounce2 {
  animation-delay: -1.0s;
}

@keyframes bounce {
  0%,
  100% {
    -webkit-transform: scale(0, 0);
    transform: scale(0, 0);
  }
  50% {
    -webkit-transform: scale(1, 1);
    transform: scale(1, 1);
  }
}

